  padding: 0; }

.content {
  padding: 3rem;
  font-size: 16px;
  margin-top: 60px; }

input {
  outline: none;
  padding: 1rem;
  background: transparent;
  border: solid 1px #ebebeb;
  border-radius: 5px; }

.hidden {
  display: none !important; }

.inline-block {
  display: inline-block; }

.bold {
  font-weight: 600; }

.secondary-font-color {
  color: rgba(0, 0, 0, 0.5); }

.small {
  font-size: 14px; }

.card {
